.activeBlock>div {
  box-shadow: 0 0 5px #636363;
  height: 100%;
}

.uploadButton {

  width: 70%;
  height: 4rem;
  padding: 0 3rem;

  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  
  border-radius: 1.5rem;

  background: #e0ffff65;
  box-shadow: 0 0 5px #d8d8d8;

  &__active {
    box-shadow: 0 0 5px #636363;
  }

  &:hover {
    background: #bfdddd;
    cursor: pointer;
  }

}

.inputFile {
  
  position: absolute;
  opacity: 0;
  visibility: hidden;

}

.fileContainer {

  display: flex;
  justify-content: center;

  width: 100%;
  height: 70vh;

  margin-top: 1.25rem;

  border-radius: 1rem;

  background: #e0ffff65;
  box-shadow: 0 0 5px #d8d8d8;
  
}

.fileList {

  position: relative;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: left;
  
  box-sizing: border-box;

  width: 100%;

  padding: 1rem 1.25rem;

}

.fileRow {
  
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;

  font-family: monospace;

  height: 30px;
  width: 100%;

  padding: 0.25rem;
  margin-bottom: 0.25rem;
  box-shadow: 0 0 2px #d8d8d8;
  box-sizing: border-box;

}

.fileName {
  font-weight: bold;
}

.noFiles {

  display: flex;
  align-items: center;
  justify-content: center;

}